<script>
import Vue from 'vue'
import attachApi from './magic/attachWindowApi.js'
import MagicCircle from './magic-circle.js'

  export default {
    name: 'downLoad',
    methods: {
      functionA() {
        let jsonData = JSON.parse(window.localStorage.dataHero)//
        console.log(jsonData, 'localhost')
        console.log(jsonData.length, '')
        let [str, title] = [jsonData.length + '', jsonData.length + ''];
        // title = jsonData.length + '';
        // console.log(str)
        //\t
        for(let i = 0 ; i < jsonData.length ; i++ ){
          for(let item in jsonData[i]){
              str+=`${jsonData[i][item] + '\t'},`; 
          }
          str+='\n';
        }
        //encodeURIComponent
        let uri = 'data:text/csv;charset=utf-8,\ufeff' + encodeURIComponent(str);
        //a
        var link = document.createElement("a");
        link.href = uri;
        //
        link.download = title;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.localStorage.clear();
      }
    }
  }
</script>

<template>
  <div>
    <!-- <main-layout> -->
      <div @click="functionA"></div>
    <!-- </main-layout> -->
  </div>
</template>

<style>
