<script>
/**
 * 
 */
import attachApi from './magic/attachWindowApi.js'
import MagicCircle from './magic-circle.js'
import Vue from 'vue'
Event=new Vue()
let IP = 'http://127.0.0.1:8081'
export default {
    name: 'app',
    data () {
        return {
          master: 1, // 
          servant: 1, // 
          cacheNameList: [],//

          cacheNameArr: [],//

          beginPolling: null, //
          heroNumber: 1, // 
          isLogoTop: false,
          ratial : '0em', //
          heroRoll: [], //  {Array}
          arrList: [],
          widthSize: 0,
          singleRow: true,

          newHerosList: [], // calledHeros

          token: '2017',

          status: 'init', // init, started, stoped, showAlled

          // 
          depList: [{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""},{"cardid":"","name":"","dep":""}],
          //
          aixuexi_teachList: [] //

        }
    },
    watch:{
      heroRoll(val){
        if (val.length > 0) {
          let length = val.length;
          let width = length < 5 ? (100/length).toFixed(2) : 20;
          if (length == 6) {
              width = (100/3).toFixed(2);
          }
          if (length > 10) {
            width = 16
          }
          this.widthSize = width;
          this.singleRow = length > 5? false: true;
          if(length>5 && length<13) {
            this.doubleRow = true;
          } else {
            this.doubleRow = false;
          }
          let contentwidth = $('.content').width();
          let contentheigth = $('.content').height();
          let tempRatial = 0;
          if (this.singleRow == true) {
              // tempRatial = (20*contentheigth/contentwidth).toFixed(2);
              tempRatial = '6em'
          }
          else if (length > 10) {
              tempRatial = '0.8em';
          }
          else {
              tempRatial = '0.3em';
          }
          this.ratial = tempRatial;
        }
      }
    },
    mounted() {
        this.$nextTick(() => {
            $.ajax({
              url: `${IP}/getAllHeros`,
            }).done((res) => {
              console.log(res, '--res123--')
              this.newHerosList = res.data
              //:dep
              this.traverseNewHeroList(this.newHerosList)
              console.info(`%c${this.cacheNameList.length}-%s`,"color: red;background: yellow;font-size: 20px",
                    `${this.newHerosList.length}`)
            }).fail((err) => {
                console.error('--getAllHeros--', err)
            })

            window.start = this.startSummon
            window.openDraw = this.showContent
            window.end = this.end
            window.winners = this.showResult
        })
    },
    methods: {
        /**
         * 
         * 
         */
        startSummon(num) {
            if(this.status === 'started' ) {
                console.error(`${this.status}-${this.cacheNameList.length}`, 'end  winners')
                return ;
            }
            if (num > 24) {
                alert('24')
                console.error(`${this.status}-${this.cacheNameList.length}`, 'start24')
                return;
            }
            if(this.cacheNameList.length > 24) {
                console.warn(`${this.status}-${this.cacheNameList.length}-${this.master}-${this.servant}%c%s`,"color: red;background: yellow;font-size: 20px",
                    '24')
            }
            if(!this.isLogoTop) {
                this.showContent()
            }
            this.heroNumber = num;
            // BGM start
            this.$refs.drawAudio.currentTime = 0;
            this.$refs.drawAudio.pause();
            this.$refs.rollAudio.play();
            // 
            this.beginPolling = setInterval( () => {
            /*

              this.newHerosList 
              this.depList 
              this.aixuexi_teachList 
              
            */
            let result = MagicCircle.call(this.newHerosList, num)
            this.heroRoll = result.calledHeros
            }, 100)

            $.ajax({
                url: `${IP}/gettoken`
            }).done((rs) => {
                if(+rs.errno === 0) {
                    this.token = rs.data
                    this.status = 'started'
                    console.info(`${this.status}-${this.cacheNameList.length}-${this.master}-${this.servant}%c%s`,"color: red;background: yellow;font-size: 20px",
                        'end')
                } else {
                    console.warn('startSummon errno!=0: ', rs.status)
                }
            }).fail((err) => {
                console.warn('startSummon: ', err)
            })
        },
        nowSummon(num) {
            //  
            // BGM start: 
            this.$refs.drawAudio.play()
            setTimeout(() => {
                $.ajax({
                    url: `${IP}/rollMyHeros`, // 
                    dataType: 'json',
                    data: {
                        token: this.token, // token
                        master: this.master,
                        servant: this.servant,
                        num: num
                    }
                }).done((rs) => {
                  console.log(rs, '--rs--')
                    if(+rs.errno === 0) {
                        clearInterval(this.beginPolling)
                        this.beginPolling = null
                        /*
                          
                        */

                        this.heroRoll = rs.data.calledHeros || []//

                        /*
                          
                        */
                        // this.heroRoll =this.heroRoll //  

                        this.newHerosList = rs.data.newHerosList || []
                        console.log(this.heroRoll, '--this.heroRoll---')
                        this.cacheNameList.push(...this.heroRoll)
                        let dataHero = JSON.stringify(this.cacheNameList)//localstrage
                        window.localStorage.dataHero = dataHero
                        // //
                        // this.getHreoListName(this.cacheNameList)
                        this.servant++
                        console.info(`${this.status}-${this.cacheNameList.length}-${this.master}-${this.servant}- :${this.newHerosList.length}%c%s`,"color: red;background: yellow;font-size: 20px"
                        , 'winners  start')
                        if(this.cacheNameList.length > 24) {
                            console.warn(`${this.status}-${this.cacheNameList.length}-${this.master}-${this.servant}%c%s`,"color: red;background: yellow;font-size: 20px",
                                '24')
                        }
                    } else {
                        console.warn('rollMyHeros', rs.status)
                    }
                }).fail((err) => {
                    console.warn('rollMyHeros', err)
                })
            }, 0)
        },
        showContent() {
            if(!this.isLogoTop) {
                this.isLogoTop = true
            }
            $(this.$refs.titlePic).animate({top: "4.5em"},1000)
        },
        end(){
            if(this.status !== 'started') {
                console.error(`${this.status}-${this.cacheNameList.length}`, '')
                return;
            }

          this.$refs.rollAudio.pause();
          this.nowSummon( this.heroNumber)
          this.status = 'stoped'
        },
        showResult(){
            if(this.status !== 'stoped') {
                console.error(`${this.status}-${this.cacheNameList.length}`, '')
                return;
            }
            this.master++
            this.servant = 1
            this.heroRoll = this.cacheNameList;
            this.cacheNameList = [];
            this.status = 'showAlled'
            this.arrList.push(this.heroRoll)
            console.log(this.arrList, '--this.arrList--')
            console.info(`${this.status}-${this.cacheNameList.length}-${this.master}-start%c%s`,"color: red;background: yellow;font-size: 20px",
             'start')
        },

        //
        traverseNewHeroList(newList) {
          for(let i of newList) {
            if(i.dep === '') {
              this.aixuexi_teachList.push(i)
            }
          }
          console.log(this.aixuexi_teachList, '--this.aixuexi_teachList--')
        }
    }
}
</script>

<template>
 <!-- <main-layout> -->
   
  <div id="app">
    <div class="header">
    </div>
    <div class="content">
      <div :style="{marginTop: ratial}">
        <ul class="employee-list">
          <!-- <li v-for="hero in heroRoll" :style="{width: widthSize+'%'}" :class="{singlerow: singleRow, doublerow: doubleRow}"> -->
          <li v-for="hero in heroRoll" :style="{width: 20+'%'}" :class="{singlerow: singleRow, doublerow: doubleRow}">
            <div class="name-normal">
              <p class="hero-info">{{hero.cardid}}</p>
              {{hero.name}}<br/>
              <!-- <br/> -->

              <p class="hero-info">{{hero.dep}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div ref="titlePic" class="title-pic-wrapper">
      <div class="title-pic" @click="showContent"></div>
    </div>
    <!-- <div class="handleToDown">
      <span @click="handleToDown" ></span>
    </div> -->
    <audio loop="loop" ref="rollAudio">
      <source src="./src/assets/audio/drum.mp3" type="audio/mpeg" />
    </audio>
    <audio ref="drawAudio">
      <source src="./src/assets/audio/goal.mp3" type="audio/mpeg" />
    </audio>
  </div>
  <!-- </main-layout> -->

</template>

<style>
@import "./css/index.css";
</style>



